
.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	color: var(--header-text);
	text-align: center;
	border-bottom: 1px solid var(--project-card-text);
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
	color: var(--project-card-text);
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	color: var(--project-card-text);
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}